<template>
  <div>
    <da-manage-page>
      <div slot="button">
        <el-button icon="my-zengjia" @click="dialogVisible=true;orgHideNode = '';getAllList();">新增</el-button>
        <el-button icon="my-zengjia" @click="meterSetting($refs.meterTb.getCheckedRows())">采集项批量配置</el-button>
      </div>
      <el-form slot="search" :inline="true" :model="searchForm.formdata">
        <el-form-item label="表计名称">
          <el-input v-model="searchForm.formdata.name"></el-input>
        </el-form-item>

        <el-form-item label="表计类型">
          <el-select v-model="searchForm.formdata.instrumentId" filterable placeholder="请选择">
            <el-option v-for="item in instrumentIdOptions" :key="item.dataValue" :label="item.dataText" :value="parseInt(item.dataValue)"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="计量级别">
          <el-select v-model="searchForm.formdata.grade" filterable placeholder="请选择">
            <el-option v-for="item in gradeOptions" :key="item.dataValue" :label="item.dataText" :value="parseInt(item.dataValue)"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="关联能源">
          <el-select v-model="searchForm.formdata.entEnergyId" filterable placeholder="请选择">
            <el-option v-for="item in entEnergyIdOptions" :key="item.dataValue" :label="item.dataText" :value="parseInt(item.dataValue)"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="getTbList">查询</el-button>
        </el-form-item>
      </el-form>
      <da-table ref="meterTb" slot="content" :colums="colsData" :table-list="tableList" :search-data="searchForm" @callBack="getTbList" :show-selection="true">
        <el-table-column property="operation" label="操作" width="210" slot="operation" align="center" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" @click="editTb(scope.row)">编辑</el-button>
            <el-button size="small" @click="meterSetting([scope.row])">采集项配置</el-button>
            <el-button size="small" @click="delTb(scope.row)">删除</el-button>
          </template>
        </el-table-column>

      </da-table>
    </da-manage-page>
    <da-dialog :title="title" :visible.sync="dialogVisible" @close="closeDialog('addForm')" @open="openDialog">
      <el-form label-width="110px" class="fm-line-50p" :model="addForm" :rules="rules" ref="addForm">
        <el-form-item label="表计名称" prop="name">
          <el-input auto-complete="off" v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="表计编号" prop="no">
          <el-input auto-complete="off" v-model="addForm.no"></el-input>
        </el-form-item>
        <el-form-item label="表计类型" prop="instrumentId">
          <el-select v-model="addForm.instrumentId" filterable placeholder="请选择">
            <el-option v-for="item in instrumentIdOptions" :key="item.dataValue" :label="item.dataText" :value="parseInt(item.dataValue)"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="计量级别" prop="grade">
          <el-select v-model="addForm.grade" filterable placeholder="请选择">
            <el-option v-for="item in gradeOptions" :key="item.dataValue" :label="item.dataText" :value="parseInt(item.dataValue)"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="关联能源" prop="entEnergyId">
          <el-select v-model="addForm.entEnergyId" filterable placeholder="请选择">
            <el-option v-for="item in entEnergyIdOptions" :key="item.dataValue" :label="item.dataText" :value="parseInt(item.dataValue)"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="是否自动采集" prop="isAutoCollect">
          <el-radio-group v-model="addForm.isAutoCollect">
            <el-radio v-for="item in isAutocollectOptions" :key="item.dataValue" :label="parseInt(item.dataValue)">{{item.dataText}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="是否停用" prop="isStopUse">
          <el-radio-group v-model="addForm.isStopUse">
            <el-radio v-for="item in isStopuseOptions" :key="item.dataValue" :label="parseInt(item.dataValue)">{{item.dataText}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="计量种类" prop="type">
          <el-radio-group v-model="addForm.type">
            <el-radio v-for="item in typeOptions" :key="item.dataValue" :label="parseInt(item.dataValue)">{{item.dataText}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="使用环节" prop="purpose">
          <!-- <el-input auto-complete="off" v-model="addForm.purpose"></el-input> -->
          <el-select v-model="addForm.purpose" filterable placeholder="请选择">
            <el-option v-for="item in purposeOptions" :key="item.dataValue" :label="item.dataText" :value="parseInt(item.dataValue)"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="ct" prop="ct">
          <el-input auto-complete="off" v-model="addForm.ct"></el-input>
        </el-form-item>
        <el-form-item label="pt" prop="pt">
          <el-input auto-complete="off" v-model="addForm.pt"></el-input>
        </el-form-item>
        <el-form-item label="表底数" prop="originalData">
          <el-input auto-complete="off" v-model="addForm.originalData"></el-input>
        </el-form-item>
        <el-form-item label="安装地址" prop="address">
          <el-input auto-complete="off" v-model="addForm.address"></el-input>
        </el-form-item>
        <el-form-item label="出厂编号" prop="manufacturerNo">
          <el-input auto-complete="off" v-model="addForm.manufacturerNo"></el-input>
        </el-form-item>
        <el-form-item label="企业管理编号" prop="manageNo">
          <el-input auto-complete="off" v-model="addForm.manageNo"></el-input>
        </el-form-item>
        <el-form-item label="安装日期" prop="installDate">
          <!-- <el-input auto-complete="off" v-model="addForm.installDate"></el-input> -->
          <el-date-picker v-model="addForm.installDate" type="date" placeholder="选择日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="校验/校准周期" prop="checkPeriod">
          <el-input auto-complete="off" v-model="addForm.checkPeriod"></el-input>
        </el-form-item>
        <el-form-item label="排序" prop="showSort">
          <el-input auto-complete="off" v-model="addForm.showSort"></el-input>
        </el-form-item>
        <el-form-item label="是否虚拟点" prop="isInvented">
          <el-radio-group v-model="addForm.isInvented">
            <el-radio v-for="item in isInventedOptions" :key="item.dataValue" :label="parseInt(item.dataValue)">{{item.dataText}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="用能对象" prop="orgId">
          <el-select v-model="addForm.orgId">
            <el-option v-for="item in orgIdOptions" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="上级回路" prop="parentId">
          <!-- <el-input auto-complete="off" v-model="addForm.parentId"></el-input>   :hide-node="orgHideNode"-->
          <da-select-tree v-model="addForm.parentId" id="meOrgTree" ref="meOrgTree" :data="parentIdTree" :hide-node="orgHideNode" :props="{label:'name',children:'children'}" @change="selectParentNode" style-attr="bottom: 40px;"></da-select-tree>
        </el-form-item>
        <el-form-item label="检验到期日期" prop="dueDate">
          <!-- <el-input auto-complete="off" v-model="addForm.dueDate"></el-input> -->
          <el-date-picker v-model="addForm.dueDate" type="date" placeholder="选择日期"></el-date-picker>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveDialog('addForm')">确 定</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>

    <da-dialog title="采集项批量配置" :visible.sync="dialogSettingVisible" size="800px">
      <da-table ref="meterItemTb" id="meterItemTb" :colums="meterItemTb" :table-list="meterDataLists" select-attr="checked" :show-selection="true"></da-table>
      <span slot="footer">
        <el-button @click="saveMeterItem" type="primary">保存</el-button>
        <el-button @click="dialogSettingVisible=false">关闭</el-button>
      </span>
    </da-dialog>
  </div>
</template>
<script>
import { getTreeByList } from "@/util/util.js";
import { difference, deepJSONCopy } from "../../../util/util";
export default {
  name: "meter",
  data() {
    return {
      instrumentIdOptions: [],
      orgIdOptions: [],
      parentIdTree: [],
      entData: [],
      orgHideNode: "",
      gradeOptions: [
        { dataText: "进出用能单位", dataValue: "1" },
        {
          dataText: "进出主要次级用能单位",
          dataValue: "2"
        },
        { dataText: "主要用能设备", dataValue: "3" }
      ],
      entEnergyIdOptions: [],
      isAutocollectOptions: [
        { dataText: "是", dataValue: "1" },
        { dataText: "否", dataValue: "0" }
      ],
      isStopuseOptions: [
        { dataText: "正常使用", dataValue: "1" },
        { dataText: "停用", dataValue: "0" }
      ],
      typeOptions: [
        { dataText: "能源", dataValue: "1" },
        { dataText: "非能源", dataValue: "2" }
      ],
      isInventedOptions: [
        { dataText: "是", dataValue: "1" },
        { dataText: "否", dataValue: "0" }
      ],
      purposeOptions: [
        { dataValue: 1, dataText: "主要生产系统" },
        { dataValue: 2, dataText: "辅助生产系统" },
        { dataValue: 3, dataText: "加工转换系统" },
        { dataValue: 4, dataText: "附属损耗其它" }
      ],
      // 查询表单
      searchForm: {
        formdata: { name: "", instrumentId: "", grade: "", entEnergyId: "" },
        currentPage: 1,
        pageSize: 20
      },
      tableList: {}, // 数据列表
      colsData: [
        // 表头

        {
          label: "表计名称",
          prop: "name",
          align: "left",
          width: "120px",
          sortable: true
        },
        {
          label: "用能对象",
          prop: "orgId",
          align: "left",
          width: "120px",
          sortable: true
        },
        {
          label: "表计编号",
          width: "140px",
          prop: "no",
          align: "left",
          sortable: true
        },
        {
          label: "表计类型",
          prop: "instrumentId",
          align: "left",
          width: "110px",
          sortable: true,
          displace: {}
        },
        {
          label: "计量级别",
          prop: "grade",
          width: "160px",
          align: "left",
          sortable: true,
          displace: {}
        },
        {
          label: "关联能源",
          prop: "entEnergyId",
          align: "left",
          width: "110px",
          sortable: true,
          displace: {}
        },
        {
          label: "是否自动采集",
          prop: "isAutoCollect",
          align: "left",
          width: "130px",
          sortable: true,
          displace: {}
        },
        {
          label: "是否停用",
          prop: "isStopUse",
          align: "left",
          width: "105px",
          sortable: true,
          displace: {}
        },
        {
          label: "计量种类",
          prop: "type",
          align: "left",
          width: "105px",
          sortable: true,
          displace: {}
        },
        {
          label: "使用环节",
          prop: "purpose",
          align: "left",
          sortable: true,
          width: "120px",
          displace: {}
        },
        { label: "ct", prop: "ct", align: "left", sortable: true },
        { label: "pt", prop: "pt", align: "left", sortable: true },
        {
          label: "表底数",
          prop: "originalData",
          width: "100px",
          align: "left",
          sortable: true
        },
        {
          label: "安装地址",
          prop: "address",
          width: "105px",
          align: "left",
          width: "105px",
          sortable: true
        },
        {
          label: "出厂编号",
          prop: "manufacturerNo",
          width: "105px",
          align: "left",
          sortable: true
        },
        {
          label: "企业管理编号",
          prop: "manageNo",
          width: "130px",
          align: "left",
          sortable: true
        },
        {
          label: "安装日期",
          prop: "installDate",
          align: "left",
          format: "YYYY-MM-DD",
          width: "105px",
          sortable: true
        },
        {
          label: "校验/校准周期",
          prop: "checkPeriod",
          align: "left",
          width: "140px",
          sortable: true
        },
        { label: "排序", prop: "showSort", align: "left", sortable: true },
        {
          label: "虚拟点",
          prop: "isInvented",
          align: "left",
          width: "100px",
          sortable: true,
          displace: {}
        },
        {
          label: "上级回路",
          prop: "parentId",
          align: "left",
          sortable: true,
          width: "120px",
          displace: {}
        },
        {
          label: "检验到期日期",
          prop: "dueDate",
          align: "left",
          format: "YYYY-MM-DD",
          width: "130px",
          sortable: true
        }
      ],
      dialogVisible: false,
      addForm: {
        name: "",
        no: "",
        instrumentId: "",
        grade: "",
        entEnergyId: "",
        isAutoCollect: 1,
        isStopUse: 1,
        type: 1,
        purpose: 1,
        ct: "1",
        pt: "1",
        originalData: "0",
        address: "",
        manufacturerNo: "",
        manageNo: "",
        installDate: "",
        checkPeriod: "",
        showSort: "",
        isInvented: 0,
        orgId: "",
        parentId: "",
        dueDate: ""
      },
      rules: this.eachValidate({
        name: { maxString: "40" },
        no: { maxString: "20" },
        instrumentId: {},
        grade: {},
        entEnergyId: {},
        isAutoCollect: {},
        isStopUse: {},
        type: {},
        purpose: {},
        ct: { digits: true },
        pt: { digits: true },
        originalData: {},
        address: { maxString: "50" },
        manufacturerNo: { maxString: "50" },
        manageNo: { maxString: "50" },
        installDate: {},
        checkPeriod: { max: 127 },
        showSort: { digits: true },
        isInvented: {},
        orgId: {},
        parentId: {},
        dueDate: {}
      }),
      // 采集项配置
      dialogSettingVisible: false,
      meterDataLists: [],
      meterItemInitCheck: [], // 默认选中的采集项
      meterIds: "",
      meterItemTb: [
        {
          label: "采集项名称",
          prop: "energyItemName",
          sortable: true
        },
        {
          label: "数据上限",
          prop: "upperValue",
          sortable: true
        },
        {
          label: "数据下限",
          prop: "lowerValue",
          sortable: true
        },
        {
          label: "表码倍率",
          prop: "rate",
          sortable: true
        },
        {
          label: "能耗数据倍率",
          prop: "dataRate",
          sortable: true
        },
        {
          label: "非工作时间上限值",
          prop: "name",
          sortable: true
        }
      ]
    };
  },
  computed: {
    title() {
      //弹窗标题
      return this.addForm.id ? "编辑表计资料管理" : "新增表计资料管理";
    },
    urlType() {
      //URL请求方式
      return this.addForm.id ? "put" : "post";
    }
  },
  watch: {},
  methods: {
    getTbList() {
      let formName = "meterTb";
      this.$ajax
        .get("rest/meter/page", {
          params: this.searchForm,
          loadingTarget: this.$refs[formName]
        })
        .then(res => {
          if (res.data.status) {
            this.tableList = res.data;
          }
        });
    },
    //组织架构树
    getOrgTree() {
      this.$ajax.get("rest/enterprise/list", { noLoading: true }).then(res => {
        if (res.data.status) {
          this.entData = res.data.records;
          // let toObj = {};
          // this.$each(res.data.records, item => {
          //   toObj[item.id] = item.name;
          // });
          // this.columns[1].displace = toObj;
        }
      });
    },
    // 上级回路
    getAllList() {
      this.$ajax
        .get("rest/meter/page", {
          params: {
            formdata: {
              name: "",
              instrumentId: "",
              grade: "",
              entEnergyId: ""
            },
            currentPage: 1,
            pageSize: 9999
          }
        })
        .then(res => {
          if (res.data.status) {
            this.parentIdTree = getTreeByList(
              res.data.records,
              "id",
              "parentId"
            );
            let lists = {};
            this.$each(res.data.records, item => {
              lists[item.id] = item.name;
            });
            this.colsData[20].displace = lists;
          }
        });
    },
    // 表计类型
    getInstrumentIdInfo() {
      this.$ajax.get("rest/meter/instrumentId/get").then(res => {
        if (res.data.status) {
          this.instrumentIdOptions = res.data.records;
          let lists = {};
          this.$each(res.data.records, item => {
            lists[item.dataValue] = item.dataText;
          });
          this.colsData[3].displace = lists;
          if (res.data.records.length > 0) {
            this.addForm.instrumentId = res.data.records[0].dataValue;
          }
        }
      });
    },
    // 关联能源
    getEntEnergyIdInfo() {
      this.$ajax.get("rest/meter/entEnergyId/get").then(res => {
        if (res.data.status) {
          this.entEnergyIdOptions = res.data.records;
          let lists = {};
          this.$each(res.data.records, item => {
            lists[item.dataValue] = item.dataText;
          });
          this.colsData[5].displace = lists;
          if (res.data.records.length > 0) {
            this.addForm.entEnergyId = res.data.records[0].dataValue;
          }
        }
      });
    },
    // 用能对象
    getOrgIdInfo() {
      this.$ajax
        .get("rest/enterpriseOrg/page", {
          params: {
            formdata: { name: "", type: "" },
            currentPage: 1,
            pageSize: 9999
          },
          noLoading: true
        })
        .then(res => {
          if (res.data.status) {
            this.orgIdOptions = res.data.records;
            let lists = {};
            this.$each(res.data.records, item => {
              lists[item.id] = item.name;
            });
            this.colsData[1].displace = lists;
          }
        });
    },
    openDialog() {
      // this.getAllList();
    },
    editTb(row) {
      this.dialogVisible = true;
      this.addForm = JSON.parse(JSON.stringify(row));
      this.orgHideNode = row.id;
    },
    closeDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.addForm = {
          name: "",
          no: "",
          instrumentId: this.instrumentIdOptions[0].dataValue,
          grade: "",
          entEnergyId: this.entEnergyIdOptions[0].dataValue,
          isAutoCollect: 1,
          isStopUse: 1,
          type: 1,
          purpose: 1,
          ct: "1",
          pt: "1",
          originalData: "0",
          address: "",
          manufacturerNo: "",
          manageNo: "",
          installDate: "",
          checkPeriod: "",
          showSort: "",
          isInvented: 0,
          orgId: "",
          parentId: "",
          dueDate: ""
        };
      });
    },
    saveDialog(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax[this.urlType]("rest/meter", this.addForm).then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.dialogVisible = false;
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
        }
      });
    },
    delTb(row) {
      this.$confirm("是否删除该记录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$ajax
          .delete("rest/meter", { params: { id: row.id } })
          .then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
      });
    },
    selectParentNode(node, lastNode) {
      if (node.id === this.addForm.id) {
        this.$message.error("用能对象不能以自身作为父节点。");
        // this.addForm.parentId = "";
        this.$set(this.addForm, "parentId", lastNode.id);
      }
    },
    initTableDisplay() {
      let gradeOptionsLists = {};
      this.$each(this.gradeOptions, item => {
        gradeOptionsLists[item.dataValue] = item.dataText;
      });
      this.colsData[4].displace = gradeOptionsLists;

      let isAutocollectLists = {};
      this.$each(this.isAutocollectOptions, item => {
        isAutocollectLists[item.dataValue] = item.dataText;
      });
      this.colsData[6].displace = isAutocollectLists;

      let isStopuseOptionsLists = {};
      this.$each(this.isStopuseOptions, item => {
        isStopuseOptionsLists[item.dataValue] = item.dataText;
      });
      this.colsData[7].displace = isStopuseOptionsLists;

      let typeOptionsLists = {};
      this.$each(this.typeOptions, item => {
        typeOptionsLists[item.dataValue] = item.dataText;
      });
      this.colsData[8].displace = typeOptionsLists;

      let isInventedOptionsLists = {};
      this.$each(this.isInventedOptions, item => {
        isInventedOptionsLists[item.dataValue] = item.dataText;
      });
      this.colsData[19].displace = isInventedOptionsLists;

      let purposeOptionsLists = {};
      this.$each(this.purposeOptions, item => {
        purposeOptionsLists[item.dataValue] = item.dataText;
      });
      this.colsData[9].displace = purposeOptionsLists;
    },
    meterSetting(rows) {
      if (rows.length === 0) {
        this.$message.info("请选择表计。");
        return false;
      }
      this.meterIds = rows;
      let meterIds = this.meterIds.map(item => item.id).join(",");
      this.$ajax
        .get("rest/meter/meterItemList", {
          params: { meterIds }
        })
        .then(res => {
          if (res.data.status) {
            this.dialogSettingVisible = true;
            this.meterItemInitCheck = res.data.records.filter(item => {
              return item.checked;
            });
            this.$nextTick(() => {
              this.meterDataLists = res.data.records;
            });
          }
        });
    },
    saveMeterItem() {
      let saveList = [];
      // 选中的表计id
      let meterIds = this.meterIds.map(item => item.id);
      // 获取新增和删除项
      let obj = difference(
        this.meterItemInitCheck,
        this.$refs.meterItemTb.getCheckedRows(),
        "energyItemId"
      );
      // 获取公共项
      let common = difference(
        this.meterItemInitCheck,
        obj.deleteList,
        "energyItemId"
      ).deleteList;
      // 因查询值为并集，顾先判断check是否为公共拥有
      common.forEach(item => {
        // 采集项内存在的表计id
        let meterCheckIds = item.id.split(",").map(id => id.split("_")[1]);
        meterIds.forEach(id => {
          if (meterCheckIds.indexOf(id.toString()) === -1) {
            let list = deepJSONCopy(item);
            list.meterId = id;
            list.id = "";
            saveList.push(list);
          }
        });
      });

      // 处理新增部分
      obj.saveList.forEach(item => {
        meterIds.forEach(id => {
          let list = deepJSONCopy(item);
          list.meterId = id;
          saveList.push(list);
        });
      });
      // 处理删除部分
      let deleteList = [];
      obj.deleteList.forEach(item => {
        let splitId = item.id.split(",").forEach(id => {
          let list = deepJSONCopy(item);
          let splitMeterId = id.split("_");
          list.id = splitMeterId[0];
          list.meterId = splitMeterId[1];
          deleteList.push(list);
        });
      });

      this.$ajax
        .post("rest/meter/meterItem", {
          saveList: saveList,
          updateList: [], // XXX 此处应存修改过的采集项行数据
          deleteList: deleteList
        })
        .then(res => {
          if (res.data.status) {
            this.$message.success(res.data.answerMsg);
            this.dialogSettingVisible = false;
          }
        });
    }
  },
  mounted() {
    this.initTableDisplay();
    this.getInstrumentIdInfo();
    this.getEntEnergyIdInfo();
    this.getOrgIdInfo();
    this.getOrgTree();
    this.getAllList();
    this.getTbList();
  }
};
</script>



